/* ---base rules--- */
/* variables */
:root {
  --appBackground: hsl(207, 26%, 17%);
  --elementBackground: hsl(209, 23%, 22%);
  --searchIconFill: white;
  --iconFill: white;
  --textColor: white;
}

button,
input,
select,
a {
  font-family: "Nunito Sans", sans-serif;
  color: inherit;
}

a {
  text-decoration: none;
  display: inline-block;
  height: 100%;
  width: 100%;
}

div svg {
  max-inline-size: 100%;
}
div img {
  max-inline-size: 100%;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

/* ---layout--- */
.header,
.main {
  padding: 0 5vw;
}

/* ---modules---- */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__theme__switch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 110px;
}
.header__theme__icon {
  max-width: 20px;
}

.countriesSearchPage {
  margin: 20px 0;
}
.countriesSearchPage section {
  margin: 10px 0;
}

.search {
  position: relative;
}
.search__icon {
  height: 24px;
  position: absolute;
  top: 11.5px;
  left: 18px;
}
.search__input {
  box-sizing: border-box;
  width: 100%;
  height: 45px;
  padding: 0 10px 0 60px;
  border: none;
  border-radius: 5px;
  font-size: inherit;
}

.regionMenu {
  position: relative;
  display: inline-block;
}
.regionMenu select {
  width: 200px;
  height: 45px;
  padding: 0 16px;
  border: none;
  border-radius: 5px;
  font-size: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.regionMenu__icon {
  height: 14px;
  fill: white;
  position: absolute;
  right: 16px;
  top: 15px;
}

.noSearchResult {
  text-align: center;
}

.countryDataLabel {
  font-weight: 800;
  font-size: 1.05em;
}

.countriesList {
  display: grid;
  max-width: 1380px;
  margin: auto;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 60px;
}
.countriesList__countryGeogrpahy {
  padding: 20px;
  line-height: 1.8;
}
.countriesList__countryGeogrpahy li:first-child {
  padding-bottom: 10px;
  font-size: 1.4em;
}

.countryItem {
  border-radius: 5px;
}
.countryItem img {
  border-radius: 5px 5px 0 0;
  width: 100%;
}

.countryPage__backToSearch {
  display: inline-block;
  padding: 5px 10px;
  margin: 30px 0;
  border-radius: 5px;
  width: initial;
}

.countryPage {
  line-height: 2.4;
}
.countryPage ul {
  margin-bottom: 40px;
}
.countryPage__name {
  font-weight: 800;
  font-size: 2em;
}
.countryPage__bordersLabel {
  font-weight: 800;
  font-size: 1.05em;
}
.countryPage__noBorders {
  font-size: initial;
  font-weight: initial;
}

.countryPage_bordersList {
  display: flex;
  flex-flow: row wrap;
}
.countryPage_bordersList li {
  border-radius: 5px;
  margin: 0 10px 15px 0;
}
.countryPage_bordersList li a {
  padding: 0 20px;
}

.loaderContainer {
  position: fixed;
  top: 180px;
  width: 100vw;
  display: flex;
  justify-content: center;
}
.loaderContainer .loader__icon {
  max-width: 110px;
}

/* desktop */
@media (min-width: 1440px) {
  .header,
.main {
    padding: 0 8vw;
  }
  /* search page */
  .countriesSearchPage {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .countriesSearchPage section {
    margin: 20px 0;
  }
  .countriesSearchPage .search {
    justify-self: start;
    width: 500px;
  }
  .countriesSearchPage .regionMenu {
    justify-self: end;
  }
  .countriesSearchPage section:last-child {
    grid-column: 1/3;
  }
  .countriesSearchPage .countriesList {
    max-width: none;
  }
  /* country page */
  .countryPage__data {
    display: flex;
    margin: 50px 0;
  }
  .countryPage__flag {
    align-self: center;
    flex: 1;
  }
  .countryPage__details {
    flex: 1;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 100px;
    min-width: 600px;
  }
  .countryPage ul {
    margin: 0;
  }
  .countryPage__name, .countryPage__borders {
    grid-column: 1/3;
    margin: 0;
  }
}
/* ---theme--- */
/* dark mode */
body {
  background-color: var(--appBackground);
  color: var(--textColor);
}

select,
input,
button,
header,
.countryItem,
.countryPage__backToSearch,
.countryPage_bordersList li {
  background-color: var(--elementBackground);
}

.header__theme__switch {
  border: none;
}
.header__theme__icon {
  fill: var(--iconFill);
}

.search__icon {
  fill: var(--searchIconFill);
}

.regionMenu__icon {
  fill: var(--iconFill);
}

.loader__icon circle {
  stroke: var(--iconFill);
}

